import React, { useContext } from 'react'
import ImgCom from './ImgCom'
import styled from 'styled-components'
import { PhotoContext } from '../context'
const Home = () => {
  const { array } = useContext(PhotoContext)
  return (
    <Container>
      {array.map(el => {
        return (
          <ImgCom key={el.id} el={el} />
        )
      })}
    </Container>)
}
const Container = styled.div`
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  max-width: 90%;
  margin: auto; 
  @media (max-width: 1000px){
    grid-template-columns: 1fr 1fr;
  }
  @media (max-width: 700px){
    display: block;
    width: 100%;
    margin: 0 auto;
  }
  @media (max-width: 500px){
    display: block;
    width: 100%;
    margin: auto;
  }
  .img {
    transition: 1s;
    width: 400px;
    height: 305px;
    margin: 3rem 5rem;
    object-fit: cover;
    @media (max-width: 1000px){
      width: 300px;
      height: 250px;
      margin: 1rem 3rem;
    }
    @media (max-width: 700px){
      width: 300px;
      height: 250px;
      margin: 1rem 3rem;
  }
  @media (max-width: 700px){
    display: block;
    margin: auto;
    padding: 1rem 0;
  }
  @media (max-width: 500px){
    width: 300px;
    height: 250px;
    padding: 0.5remm 0;
  }
  }
  .img:hover {
    transform: scale(1.2);
    /* @media (max-width: 1000px){
      width: 300px;
      height: 250px;
      margin: 1rem 3rem;
    } */
  }
  div{
      position: relative;
  }
  /* .fa-trash-alt {
    position: absolute;
    top: 50px;
    right: 30px;
    z-index: 2 !important;
    color: red;
  } */
  .fa-plus{
    position: absolute;
    top: 70px;
    left: 100px;
    z-index: 2 !important;
    @media (max-width: 1000px){
      position: absolute;
      top: 40px;
      left: 60px;
    }
    @media (max-width: 700px){
      position: absolute;
      top: 40px;
      left: 170px;
  }
  @media (max-width: 500px){
    position: absolute;
    top: 40px;
    left: 40px;
  }
  }

  .fa-heart {
    position: absolute;
    top: 70px;
    right: 100px;
    z-index: 2 !important;
    color: red;
    @media (max-width: 1000px){
      position: absolute;
      top: 40px;
      right: 105px; 
    }
    @media (max-width: 700px){
      position: absolute;
      top: 40px;
      right: 170px;
  }
  @media (max-width: 500px){
    position: absolute;
    top: 40px;
    right: 40px;
  }
  }
`
export default Home