import React, { useContext } from "react";
import styled from 'styled-components'
import { Link } from "react-router-dom";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faShoppingCart } from "@fortawesome/free-solid-svg-icons";
import { PhotoContext } from '../context'
const NavbarCom = () => {
  const { cardItem } = useContext(PhotoContext)

  return (
    <div>
      <Navbar>
        <div className="container">
          <div className="navbar">
            <h1>Online Shop</h1>
            <ul className="ml-auto">
              <li><Link to="/">Home</Link></li>
              <li><Link to="/card"><FontAwesomeIcon icon={faShoppingCart} /><sup
                className="sup">{cardItem.length}</sup></Link></li>
            </ul>
          </div>
        </div>
      </Navbar>
    </div>)
}
const Navbar = styled.div`
  background-color: black;
  width: 100%;
  .container {
    max-width: 100%;
    margin: auto;
    padding: 0 5rem;
    .navbar {
      display: flex;
      justify-content: space-evenly;
      color: #FFFFFF;
      align-items: center;
      min-height: 100px;
      font-size: 24px;
      @media (max-width: 700px){
        display: flex;
        justify-content: space-between;
        align-items: center;
      .h1{
        font-size: 30px;
      }
    }
    @media (max-width: 500px){
      display: flex;
      justify-content: space-evenly;
      align-items: center;
      width: 100%;
      .h1{
        width: 10rem;
        font-size: 16px;
        display: inline-block;
      }
      .ml-auto{
        font-size: 16px;
      }
      .sup{
        padding: 0;
      }
     ul li{
       display: flex;
       justify-content: space-between;
       font-size: 18px;
     }
    }
      .ml-auto {
        margin-left: auto;
        display: flex;
        li {
          color: #FFFFFF;
          list-style: none;
          margin-right: 60px;
          .sup {
            padding: 6px;
            border-radius: 50%;
            background-color: red;
            color: #FFFFFF;
          }
          a {
            text-decoration: none;
            color: #FFFFFF;
          }
        }
        li:last-child {
          margin-right: 15px;
        }}}}
`
export default NavbarCom;
