import React, { createContext, useState, useEffect } from 'react'

const PhotoContext = createContext()
const url = 'https://raw.githubusercontent.com/bobziroll//scrimba-react-bootcamp-images/master/images.json';
const ContextProvider = ({ children }) => {
    const [array, setArray] = useState([])
    const [cardArray] = useState([])
    const [cardItem, setCardItem] = useState([])

    const getPhotos = async () => {
        const response = await fetch(url)
        const data = await response.json()
        setArray(data);
    }
    useEffect(() => {
        
        getPhotos()
    }, [])

    function addToCard(newItem){
        setCardItem([...cardItem, newItem])
    }

    const toggleFavorite = (id) => {
        const Favorite = array.map(el => {
            if (el.id === id) {
                return {
                    ...el,
                    isFavorite: !el.isFavorite
                }
            }
            return el
        })
        setArray(Favorite);
    }

    const RemoveCard = (id) => {
        let newArray = cardItem.filter(el => {
          return el.id !== id
        })
        setCardItem(newArray)
      }



    return (
        <PhotoContext.Provider value={{cardArray, array, setCardItem, cardItem, addToCard, RemoveCard, toggleFavorite}}>
            <h1>{children}</h1>
        </PhotoContext.Provider>
    )
}

export { PhotoContext, ContextProvider }
