import React, { useContext, useState } from "react";
import styled from "styled-components";
import { PhotoContext } from "../context";
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faTrash } from "@fortawesome/free-solid-svg-icons";
const Carditems = ({ el }) => {
  const [hovered, setHovered] = useState(false)
  const { RemoveCard } = useContext(PhotoContext)
  
  return (
    <Container
      onMouseLeave={() => setHovered(false)}
      onMouseEnter={() => setHovered(true)}>
      <img className="img" src={el.url} alt='' />
      {hovered && (
        <FontAwesomeIcon onClick={() => RemoveCard(el.id)} className="fatrash" icon={faTrash} />
      )}
    </Container>)}
const Container = styled.div`
   max-width: 85%;
  margin: auto;
  overflow: hidden;
  width: 400px;
  height: 400px;
  position: relative;
  .img {
    transition: 1s;
    width: 400px;
    height: 300px;
    margin: 30px 0;
    object-fit: cover;
  }
  .img:hover {
    transform: scale(1.2);
  } 
  .fatrash{
    position: absolute;
    top: 50px;
    right: 30px;
    z-index: 2 !important;
    color: red;
  }
`
export default Carditems;
