import React from 'react'
import Navbar from './components/NavbarCom'
import Home from './components/Home'
import CardCom from './components/CardCom'
import {Switch, Route} from 'react-router-dom'


function App() {
  return (
    <div className="App"> 
      <Navbar />
      <Switch>
        <Route exact path="/">
          <Home />
        </Route>
        <Route exact path="/card">
          <CardCom />
        </Route>
      </Switch>
    </div>
  );
}

export default App;
