import React, { useContext } from 'react'
import styled from "styled-components";
import { PhotoContext } from "../context";
import Carditems from './Carditems';
const CardCom = () => {
  const { cardItem } = useContext(PhotoContext)
  return (
    <Container>
      {cardItem.map((el) => {
        return(
          <Carditems key={el.id} el={el}/>
        )
      })}
    </Container>)}
const Container = styled.div`
   display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  max-width: 90%;
  margin: auto; 
 .img {
    transition: 1s;
    width: 400px;
    height: 300px;
    margin: 30px 0;
    object-fit: cover;
  }
  .img:hover {
    transform: scale(1.2);
  }
 .fatrash {
    position: absolute;
    top: 50px;
    right: 30px;
    z-index: 2 !important;
    color: red;
  }
`
export default CardCom
