import React, { useContext, useState } from 'react'
import { PhotoContext } from '../context';
import { FontAwesomeIcon } from "@fortawesome/react-fontawesome";
import { faPlus, faHeart, faHeartBroken, faShoppingCart } from "@fortawesome/free-solid-svg-icons";

const ImgCom = ({ el }) => {
  const { addToCard, toggleFavorite, RemoveCard, cardItem } = useContext(PhotoContext)

  const [hovered, setHovered] = useState(false)


  const MinusCard = () => {
    const plusArray = cardItem.find(cart => cart.id === el.id);
    if (plusArray) {
      return <FontAwesomeIcon onClick={() => RemoveCard(el.id)} className="fa-plus" icon={faShoppingCart} />
    } else {
      return <FontAwesomeIcon onClick={() => addToCard(el)} className="fa-plus" icon={faPlus} />
    } 
  }

  return (
    <div
      onMouseLeave={() => setHovered(false)}
      onMouseEnter={() => setHovered(true)}>
      <img className="img" src={el.url} alt='' />
      {hovered && (
        <>
          {MinusCard()}
          <FontAwesomeIcon onClick={() => toggleFavorite(el.id)} className='fa-heart' icon={el.isFavorite ? faHeartBroken : faHeart} />
        </>
      )}

    </div>
  )
}
export default ImgCom

